const Noodl = require('@noodl/noodl-sdk');
const { default: OpenLayersMap } = require('./OpenLayersMap');
const pointA = {
	label: 'Point A',
	longitude: 12.4924,
	latitude: 41.8902, // Koordinate za Point A (npr. Rim)
};

const pointB = {
	label: 'Point B',
	longitude: 16.4964,
	latitude: 41.9028, // Koordinate za Point B (npr. Rim)
};

const MyCustomReactComponentNode = Noodl.defineReactNode({
	name: 'Map Component',
	category: 'Tutorial',
	getReactComponent() {
		return OpenLayersMap;
	},
	inputProps: {
		isInteractive: {
			displayName: 'Is Interactive',
			group: 'Interactive',
			type: 'boolean',
			default: true
		},
		zoom: {
			displayName: 'Zoom',
			group: 'Coordinates',
			type: 'number',
			default: 2
		},
		longitude: {
			displayName: 'Longitude',
			group: 'Coordinates',
			type: 'number',
			default: 0
		},
		latitude: {
			displayName: 'Latitude',
			group: 'Coordinates',
			type: 'number',
			default: 0
		},
		pins: {
			displayName: 'Pins',
			group: 'Pins',
			type: 'array',
			default: [
				{ label: 'Pin 1', longitude: -0.1276, latitude: 51.5074, description: 'London, UK' },
				{ label: 'Pin 2', longitude: 2.3522, latitude: 48.8566, description: 'Paris, France' },
				{ label: 'Pin 3', longitude: 12.4964, latitude: 41.9028, description: 'Rome, Italy' },
			]
		},
		pointA: {
			displayName: 'Point A',
			group: 'Points',
			type: 'object', // Ispravljeno: koristimo tip 'object'
			default: {}
		},
		pointB: {
			displayName: 'Point B',
			group: 'Points',
			type: 'object', // Ispravljeno: koristimo tip 'object'
			default: {}
		},
	},
	outputProps: {
		onPinClicked: {
			type: 'signal', // Tip za output 'signal' za dogaaje
			displayName: 'On Pin Clicked'
		},
		onMapClick: {
			type: 'signal',
			displayName: 'On Map Clicked'
		},
		onViewChange: {
			type: 'signal',
			displayName: 'On View Change'
		},
	}
});

Noodl.defineModule({
	reactNodes: [
		MyCustomReactComponentNode
	],
	nodes: [],
	setup() {
		//this is called once on startup
	}
});
